import ApiService from './framework/api-service.js';
import { ApiMethod, BaseUrl } from './const.js';


export default class PointsApiService extends ApiService {

  //this._load   promis.  ,   
  //    ,   'then' (   JS ).
  get points() {
    return this._load({url: BaseUrl.POINTS})
      .then(ApiService.parseResponse);
  }

  get destinations() {
    return this._load({url: BaseUrl.DESTINATIONS})
      .then(ApiService.parseResponse);
  }

  get offers() {
    return this._load({url: BaseUrl.OFFERS})
      .then(ApiService.parseResponse);
  }

  async addPoint(point) {
    const response = await this._load({
      url: `${BaseUrl.POINTS}`,
      method: ApiMethod.POST,
      body: JSON.stringify(this.#adaptToServer(point)),
      headers: new Headers({ 'Content-Type': 'application/json'}),
    });
    const parsedResponse = await ApiService.parseResponse(response);

    return parsedResponse;
  }

  async deletePoint(point) {
    const response = await this._load({
      url: `${BaseUrl.POINTS}/${point.id}`,
      method: ApiMethod.DELETE,
    });
    return response;
  }


  async updatePoint(point) {
    //    this._load    
    const response = await this._load({
      url: `${BaseUrl.POINTS}/${point.id}`, //       
      method: ApiMethod.PUT, //      - PUT
      body: JSON.stringify(this.#adaptToServer(point)), // 1)    ,      2)   JSON     
      headers: new Headers({ 'Content-Type': 'application/json'}), //         JSON
    });

    //          
    const parsedResponse = await ApiService.parseResponse(response);

    return parsedResponse;

  }

  #adaptToServer(point) {
    const adaptedPoint = {...point,
      'base_price': parseInt(point.basePrice, 10),
      'date_from': point.dateFrom,
      'date_to': point.dateTo,
      'is_favorite': point.isFavorite,
    };

    delete adaptedPoint.basePrice;
    delete adaptedPoint.dateFrom;
    delete adaptedPoint.dateTo;
    delete adaptedPoint.isFavorite;
    delete adaptedPoint.description;
    delete adaptedPoint.pictures;

    return adaptedPoint;
  }

}

